export default {

  components: {
    VueTelInput,
  },

  props: {

    caption: {

      type: String,

      default: 'Managers'

    },



    hover: {

      type: Boolean,

      default: true

    },



    striped: {

      type: Boolean,

      default: true

    },



    bordered: {

      type: Boolean,

      default: false

    },



    small: {

      type: Boolean,

      default: false

    },



    fixed: {

      type: Boolean,

      default: false

    },



    dark: {

      type: Boolean,

      default: false

    }

  },



  data: () => {

    return {

      selectedManager : {

       
      },

      saveManager : {

        username : null,

        FirstName : null,

        LastName : null,

        username : null,

        isActive :false,

      

      },

      editManager : {

        username : null,

        FirstName : null,

        LastName : null,

        username : null,

        isActive :false



      },
       showLoader:false,
      bindProps: {
        defaultCountry: "PF",
        disabledFetchingCountry: false,
        disabled: false,
        disabledFormatting: false,
        placeholder: "Enter a phone number",
        required: false,
        enabledCountryCode: true,
        enabledFlags: true,
        deleteImages:"",
        deleteImgObj:{},
        image_delete_flag:false,
        preferredCountries: ["PF"],
        onlyCountries: [],
        storeObj:{},
        ignoredCountries: [],
        ApiEndPoints:"",
        autocomplete: 'on',
        name: "telephone",
        maxLen: 14,
        wrapperClasses: "",
        inputClasses: "",
        dropdownOptions: {
          disabledDialCode: false
        },
        inputOptions: { showDialCode: false, tabindex: 0 },
        validCharactersOnly:true
      },

      editUser: false,

      currentPage: 1,
       phone:"",
       CountryCode:"",
      

      sortBy : 'FirstName',

      file: null,

      perPage:20,
      filters: {
                StoreName: '',
                FirstName:''

              },

      managersList : [],

      storeList : [],

      loginUser:{},

      departmentList : [],

      countriesList1 : [

       {

          countryName: "France",

          countryDialCode:"+33"



       },

       {

          countryName: "India",

          countryDialCode:"+91"

       }



      ],

      countriesList : ["+33","+91"]



    }

  },



  computed: {

    fields () {

      return [

        {key: 'FirstName',label: this.translateString('First_Name'),  sortable: true},

        {key: 'LastName',label: this.translateString('Last_Name'),  sortable: true},

        {key: 'Email', label: this.translateString('Email'), sortable: true},

     //   {key: 'MobileNumber',label: this.translateString('Mobile_Number'),sortable: true},

        {key: 'StoreName',label: this.translateString('Store_Name'),sortable: true},

        {key: 'Role',label: this.translateString('Role'),sortable: true},

        {key: 'isActive',label: this.translateString('Status'),sortable: true},

        {key: 'actions' ,label: this.translateString('Action'),sortable:false,thStyle: {'width': '70px !important', 'min-width': '70px !important'}}

      ]

    },



    gridData2: function() {

      return this.managersList;

    },
     
      gridData () {
      const filtered = this.managersList.filter(item => {
        return Object.keys(this.filters).every(key =>
            // String(item[key]).includes(this.filters[key]))
             String(item[key]).toLowerCase().includes( String(this.filters[key]).toLowerCase()))
      })
      return filtered.length > 0 ? filtered : [{
        StoreName: '',
      GroupName: ''
      }]
    },



    totalRows: function () {

      return this.getRowCount()

    },



    captions: function() {

      return this.fields

    }

  },



  methods: {

    translateString : function(colName) {

      return this.$i18n.t(colName);

    },



    getRowCount: function () {

      return this.managersList.length;

    },



    selectObject: function(manager){

      //  console.log("selected :",manager);

       this.CountryCode=null;

      this.editManager = manager;


      this.editManager.confirm_password=this.editManager.password;

        if(this.editManager.ProfilePhotoURL==null || this.editManager.ProfilePhotoURL==""){
            this.editManager.ProfilePhotoURL=null;
        }
      this.editManager.viewProfilePhotoURL=this.editManager.ProfilePhotoURL;

      if(this.editManager.ProfilePhotoURL!=null && this.editManager.ProfilePhotoURL!=""){
      this.editManager.viewProfilePhotoURL1=this.ApiEndPoints+"images/"+this.editManager.ProfilePhotoURL;
      }else{
          this.editManager.viewProfilePhotoURL1=this.ApiEndPoints+"images/profile_photo.png";
      }
      this.editManager.Role=(this.editManager.Role=="SA"||this.editManager.Role=="SM")?this.editManager.Role="SM":this.editManager.Role="M";
      this.CountryCode=this.editManager.CountryCode;
       this.editManager.isActive=(this.editManager.isActive=="Y" || this.editManager.isActive=="Active"|| this.editManager.isActive=="true"|| this.editManager.isActive==true)?true:false;

      this.editManager.Status === "Enabled" ? this.editManager.Status = true : this.editManager.Status = false;

      // console.log('this.editManager', this.editManager);

    },



    getChanged: function(isActive){

      // console.log("isActive",isActive);

     (isActive==true||isActive=="true")?this.editManager.isActive=false:this.editManager.isActive=true;
     (isActive==true||isActive=="true")?this.saveManager.isActive=false:this.editManager.isActive=true;

      // console.log("isActive :",this.saveManager.isActive,this.editManager.isActive);

    },

    addDeleteManagerImgs: function(obj,UserID) {
       this.deleteImgObj=obj;
       this.deleteImages=UserID;
       this.editManager.viewProfilePhotoURL1=null;
      //  console.log(UserID);
      //  console.log(this.deleteImages);
        
     },

     printnumber: function(number) {
        // console.log(number);  
        // console.log("codeeeeee",this.CountryCode);   
        },

        onSelect({name, iso2, dialCode}) {

          this.CountryCode=dialCode;
      // console.log(name,iso2,dialCode);

     },
     

     deleteImg: function(Obj,id) {
 

     let url="/user/Profile/photo/remove/"+id;
    // let imgpath="https://toutpaie.kikundigital.com/images/"+img;
     toutPaieService.saveData(Obj, url).then(data => {
       // console.log('SAVEdata', form);
         
       let msg=data.data[0].message;
        if (data.status == 200 && msg=="Success") { 
          this.deleteImgObj={};
       this.deleteImages="";
         VueNotifications.success({message: this.translateString("Product_Image_Deleted_Successfully_Saved") ,timeout:2500});
          //window.location.reload();
        }else{
           VueNotifications.error({message: this.translateString(msg) ,timeout:2500});
        }   
         
      });



    },



    deleteItem: function (UserID, ref) {

      let URL = 'user/delete/' + UserID;

      toutPaieService.getData(URL).then(data => {



        if(data.message=="Success"){

          ref.hide();

          VueNotifications.success({message:this.translateString("User_Deleted_Successfully"),timeout:2500});

          this.clearObjects();

          this.getBackOfficeUsers();     

        }else{

           VueNotifications.error({message:this.translateString(msg),timeout:2500});

        }





      });

    },



    updateItem: function(refs, editManager){

      // console.log('in update function');

      this.editUser=true;

     this.saveItem(refs, editManager);

     this.editUser=false;

    },



    selectAllStore: function(selectedManager) {
      // console.log("selectedManager.role :",selectedManager.Role);
      //  console.log("saveManager.Role :",this.saveManager.Role);
      

      this.selectedManager.Role == 'M' || this.selectedManager.Role !== 'SM' ? this.saveManager.StoreID = null : this.saveManager.StoreID = 'all';

      selectedManager.Role == 'M' || selectedManager.Role !== 'SM' ? this.editManager.StoreID = null : this.editManager.StoreID = 'all';

    },
    
     validateNumber : function(){

      //  if(this.saveManager.MobileNumber<9999999999){
       
      // }else{
      //       this.showLoader=false;
      //        VueNotifications.error({message:this.translateString("Mobile_Number_Should_be_10_Digit"),timeout:2500});
      // }

     },

     
      disableEnableStore: function(selectedUser) {
      
      let url =null; 
  
        url="store/enable/"+selectedUser.StoreID;
    

    toutPaieService.saveData(selectedUser, url).then(data => {
        // console.log('data', data);
        let msg=data.data[0].message;
        if (data.status == 200 && msg=="Success") { 
             VueNotifications.success({message: selectedUser.StoreName+this.translateString("Store_Enabled_Successfully_Saved"),timeout:2500});

        }else{
           VueNotifications.error({message:this.translateString(msg),timeout:2500});
        }
      });

    },




    saveItem: function(ref, selectedManager) {

    // console.log("selectedManager :",selectedManager);

      let userData = selectedManager;

       let msg="";
       userData.CountryCode=this.CountryCode;
      

       if(this.editUser && this.deleteImages){

                   this.deleteImg(this.deleteImgObj,this.deleteImages);        
                   this.deleteImgObj={};
                   this.deleteImages="";
             
          }

          

      var form = new FormData();
     
      form.append("FirstName", userData.FirstName);
      form.append("LastName",userData.LastName);

      if(userData.MobileNumber<9999999999){
         form.append("MobileNumber",userData.MobileNumber);
         form.append("CountryCode",this.CountryCode);

      }else{

           form.append("MobileNumber","1234567890");
           form.append("CountryCode","+33");

            //  this.showLoader=false;
            //  VueNotifications.error({message:this.translateString("Mobile_Number_Should_be_10_Digit"),timeout:2500});
            //    return;
      }
      
      form.append("Email",userData.Email);
      form.append("PostalCode",userData.PostalCode);
      form.append("ProfilePhotoURL",userData.ProfilePhotoURL);
      form.append("DateOfBirth",userData.DateOfBirth);
      form.append("DepartmentID",userData.DepartmentID);
      form.append("Role",(userData.Role=="SM"?"SM":"M"));
      form.append("Status",userData.Status);


      form.append("StoreID",userData.StoreID);

    if(!this.editUser){
            var count=0;
            var msgt=null;

          for(var i=0;i<this.managersList.length;i++){

            if((this.managersList[i].StoreID==userData.StoreID)&& (userData.StoreID>0)){
                count++;
                this.showLoader=false;
                msgt="Store_Already_assigned_to"
                 VueNotifications.error({message:this.translateString(msgt)+" "+this.managersList[i].FirstName+" "+this.managersList[i].LastName,timeout:3000});
                 return;
              
            }

          }   
                       if(count==0 && userData.StoreID>0){
                              for(var k=0;k<this.storeList.length;k++){
                                      
                                      if(this.storeList[k].StoreID==userData.StoreID){         
                                          this.storeObj=this.storeList[k];

                                          this.disableEnableStore(this.storeObj);
                                        
                                      }
                              }
                       }
    }


      form.append("City",userData.City);
      form.append("StreetName",userData.StreetName);
      form.append("isActive",(userData.isActive==true?"Y":"N"));
      form.append("password",userData.password);
      form.append("confirm_password",userData.confirm_password);
      form.append("CountryCode",userData.CountryCode);

      this.CountryCode="+33";      
      let url =null; 

      if (this.editUser === true && userData.UserID) {

        url = 'user/backoffice/profile/' + userData.UserID;

      }else{

        url='/manager/save';

      }

     toutPaieService.saveData(form, url).then(data => {
      
       let msg=data.data[0].message;

        if (data.status == 200 && msg=="Success") { 
               ref.hide();
               this.showLoader=false;
               
              this.clearObjects();
              this.getBackOfficeUsers();    
          VueNotifications.success({message:this.translateString("User_Data_Successfully_Saved"),timeout:2500});

        }else{
          this.showLoader=false;
           VueNotifications.error({message:this.translateString(msg),timeout:2500});
        }          
      });


    },

    clearObjects:function(){
      this.showLoader=false;
      this.selectedManager={};
      this.CountryCode=null;
      this.editManager={};
      this.saveManager={};
      this.getBackOfficeUsers();

    },



    getBackOfficeUsers: function () {

      // this.loginUser=JSON.parse(window.localStorage.getItem("loginUser"));

      let url = 'users/backoffice';

      toutPaieService.getData(url).then(res => {

        this.managersList = res.data;

      });

    }

  },



  beforeMount () {

    this.ApiEndPoints=toutPaieService.ApiEndPoints;

    this.getBackOfficeUsers();

    toutPaieService.getData('store').then(res => {

      this.storeList = res.data;

    });

  }

}

</script>

